.dialogs {
    display: grid;
    grid-template-columns: 180px 1fr;
    border: 2px solid black;
    background-color: white;
    border-radius: 8px;
}

.dialogsList {
    border-width: 0px;
    margin-top: -1px;
}

.messages {
    overflow: hidden;
    border-radius: 0px 8px 8px 0px;
}

